	import {  
        mapMutations  
    } from 'vuex';
	
	export default{
		data(){
			return {
				username:'',
				password: '',
				showPassword: true,
				mobile: '',
				imgcode:'',
				imgyzcode:'',
				phonecode:'',
				phonetoken:'',
				scode:false,
				getcode:'获取验证码',
				logining: false,
				index:1,
				width:70,height:30
			}
		},
		onLoad(e){
			var _self = this;
			setTimeout(function(){
				_self.init();
			},100)
			uni.removeStorageSync('cbe_retoken');
			uni.removeStorageSync('cbe_token');
			if(e.code){
				this.getWxuserinfo(e.code);
			}
		},
		onShow() {
			if(uni.getStorageSync('cbe_token')){
				uni.switchTab({
					url:"/pages/index/index"
				})
			}
			uni.removeStorageSync('cbe_retoken');
			this.init();
		},
		methods: {
			...mapMutations(['login']),
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			},
			changePassword() {
				this.showPassword = !this.showPassword;
			},
			changeLogin(e){
				this.index = e;
				this.init();
			},
			navBack(){
				uni.navigateBack({
					delta:1
				});
			},
			toRegist(){
				uni.navigateTo({
					url:'zhecexieyi',
				})
			},
			toForget(){
				uni.navigateTo({
					url:'forget',
				})
			},
			wechatLogin(){


















			},
			randomNum(minNum,maxNum){ 
				switch(arguments.length){ 
					case 1: 
						return parseInt(Math.random()*minNum+1,10); 
					break; 
					case 2: 
						return parseInt(Math.random()*(maxNum-minNum+1)+minNum,10); 
					break; 
					default: 
						return 0; 
					break; 
				} 
			},
			getWxuserinfo(code){
				uni.showLoading({
					title: '正在处理',
					mask: true
				});
				var that = this;
				uni.request({
					url:uni.getStorageSync('hostname') + '?action=index/login/wxjsapiLogin',
					data: {code:code},
					method:'POST',
					dataType:'json',
					header: {'content-type' : 'application/x-www-form-urlencoded'},
					success: (resa) => {
						uni.hideLoading();
						var one = resa.data;
						if(one.code==0){
							uni.setStorageSync('cbe_token',one.token);
							uni.showToast({
								title:one.msg,icon:'success',mask:true,duration: 3000
							});
							that.login(one.data);
							var timer = setTimeout(()=>{
								// window.location.href = uni.getStorageSync('hostname')+"h5/#/pages/user/user";
								uni.switchTab({
									url:"/pages/user/user"
								})
							},3000);
						}else if(one.code==1){
							uni.setStorageSync('cbe_retoken',one.token);
							uni.showToast({
								title:one.msg,icon:'success',mask:true,duration: 3000
							});
							var timer = setTimeout(()=>{
								uni.navigateTo({
									url:"dsflogin"
								})
							},2000);
						}else{
							uni.showToast({ title:one.msg, icon: "none" });
							this.logining = false;
						}
					}
				})
			},
			getuserinfo(e){
				var that = this;
				if(e.detail.errMsg=="getUserInfo:ok"){
					uni.showLoading({
						title: '正在授权',
						mask: true
					});
					uni.login({
						provider: 'weixin',
						success: function (loginRes) {
							if(loginRes.errMsg=="login:ok"){
								var code = loginRes.code;
								uni.getUserInfo({
									provider: 'weixin',
									lang:'zh_CN',
									success: function (infoRes) {
										uni.request({
											url:uni.getStorageSync('hostname') + '?action=index/login/wechatlogin&code='+code,
											data: infoRes,
											method:'POST',
											dataType:'json',
											header: {'content-type' : 'application/x-www-form-urlencoded'},
											success: (resa) => {
												uni.hideLoading();
												var one = resa.data;
												if(one.code==0){
													uni.setStorageSync('cbe_token',one.token);
													uni.showToast({
														title:one.msg,icon:'success',mask:true,duration: 3000
													});
													that.login(one.data);
													var timer = setTimeout(()=>{
														uni.navigateBack({
															delta:1
														})
													},3000);
												}else if(one.code==1){
													uni.setStorageSync('cbe_retoken',one.token);
													uni.showToast({
														title:one.msg,icon:'success',mask:true,duration: 3000
													});
													var timer = setTimeout(()=>{
														uni.navigateTo({
															url:"/pages/public/zhecexieyi"
														})
													},2000);
												}else{
													uni.showToast({ title:one.msg, icon: "none" });
													this.logining = false;
												}
											}
										})
									}
								});
							}
						}
					});
				}
			},
			async toLogin(){
				this.logining = true;
				if(this.index==0){
					if(!this.username){
						uni.showToast({ title: '手机号码/账号不能为空', icon: 'none' });
						this.logining = false;return false;
					}
					if(!this.password){
						uni.showToast({ title: '密码不能为空', icon: 'none' });
						this.logining = false;return false;
					}
					uni.showLoading({
						title: '正在登录',
						mask: true
					});
					uni.request({
						url:uni.getStorageSync('hostname') + '?action=index/login/pwdlogin',
						data: {username:this.username,password:this.password},
						method:'POST',
						dataType:'json',
						header: {'content-type' : 'application/x-www-form-urlencoded'},
						success: (resa) => {
							uni.hideLoading();
							var one = resa.data;
							if(one.code==0){
								uni.setStorageSync('cbe_token',one.token);
								this.phonetoken = '';
								uni.showToast({
									title:one.msg,icon:'success',mask:true,duration: 3000
								});
								this.login(one.data);
								var timer = setTimeout(()=>{
									uni.navigateBack({
										delta:1
									})
								},3000);
							}else{
								uni.showToast({ title:one.msg, icon: "none" });
								this.logining = false;
							}
						}
					})
				}else{
					var reg = /^1[0-9]{10,10}$/;
					if(!this.mobile){
						uni.showToast({ title: '手机号码不能为空', icon: 'none' });
						this.logining = false;return false;
					}else{
						if(!reg.test(this.mobile)){
							uni.showToast({ title: '手机号码格式错误', icon: "none" });
							this.logining = false;return false;
						}
					}
					if(!this.imgcode){
						uni.showToast({ title: '图形验证码不能为空', icon: 'none' });
						this.logining = false;return false;
					}else{
						if(this.imgcode!=this.imgyzcode){
							uni.showToast({ title: '图形验证码错误', icon: 'none' });
							this.logining = false;return false;
						}
					}
					if(!this.phonecode){
						uni.showToast({ title: '手机验证码不能为空', icon: 'none' });
						this.logining = false;return false;
					}
					uni.showLoading({
						title: '正在登录',
						mask: true
					});
					uni.request({
						url:uni.getStorageSync('hostname') + '?action=index/login/phonelogin',
						data: {phone:this.mobile,imgcode:this.imgcode,code:this.phonecode,token:this.phonetoken},
						method:'POST',
						dataType:'json',
						header: {'content-type' : 'application/x-www-form-urlencoded'},
						success: (resa) => {
							uni.hideLoading();
							var one = resa.data;
							if(one.code==0){
								uni.setStorageSync('cbe_token',one.token);
								this.phonetoken = '';
								uni.showToast({
									title:one.msg,icon:'success',mask:true,duration: 3000
								});
								this.login(one.data);
								var timer = setTimeout(()=>{
									uni.navigateBack({
										delta:1
									})
								},3000);
							}else{
								uni.showToast({ title:one.msg, icon: "none" });
								this.logining = false;
							}
						}
					})
				}
			},
			getPhoneCode:function(e){
				if(this.scode==false){
					var reg = /^1[0-9]{10,10}$/;
					if(!reg.test(this.mobile)){
						uni.showToast({ title: '手机号码格式错误', icon: "none" });
					}else{
						if(!this.imgcode){
							uni.showToast({ title: '图形验证码不能为空', icon: 'none' });
							return false;
						}else{
							if(this.imgcode!=this.imgyzcode){
								uni.showToast({ title: '图形验证码错误', icon: 'none' });
								this.logining = false;return false;
							}
						}
						var ntime = 120;
						this.scode = true;
						this.timer = setInterval(() => {
							ntime = ntime-1;
							if(ntime=='0'){
								this.getcode = '获取验证码';
								ntime = 120;
								this.scode = false;
								clearInterval(this.timer);
							}else{
								this.getcode = '重新获取('+ntime+'s)';
							}
						},1000);
						uni.showLoading({
							title: '正在获取',
							mask: true
						});
						uni.request({
							url: uni.getStorageSync('hostname') + '?action=index/common/getphonecode', //获取手机验证码信息,
							data: {phone:this.mobile,use:'login'},
							method:'POST',
							dataType:'json',
							header: {'content-type' : 'application/x-www-form-urlencoded'},
							success: (resa) => {
								uni.hideLoading();
								var one = resa.data;
								if(one.code == 0){
									console.log(one.msg);
									uni.showToast({
										title:one.msg,icon:'success',mask:true,duration: 3000
									});
									this.phonetoken = one.token;
									this.index = 1;
								}else{
									uni.showToast({ title:one.msg,icon: "none"});
									this.getcode = '获取验证码';
									ntime = 120;
									this.scode = false;
									clearInterval(this.timer);
								}
								
							},
							fail:(resa)=> {
								uni.showToast({ title: '网络连接超时，请检查您的网络', icon: "none" });
								var timer = setTimeout(()=>{
									this.getcode = '获取验证码';
									ntime = 120;
									this.scode = false;
									clearInterval(this.timer);
								},1000);
							}
						})
					}
				}
			},
			async init(){
				var context = uni.createCanvasContext('imgcanvas',this),w=this.width,h=this.height;
				context.setFillStyle("white");
				context.setLineWidth(5);			
				context.fillRect(0, 0, w, h);
				var pool= ["A","B","C","D","E","F","G","H","J","K","L","M","N","P","Q","R","S","T","U","V","W","S","Y","Z","1","2","3","4","5","6","7","8","9","0"],str='';
				for(var i=0;i<4;i++){
					var c = pool[this.rn(0,pool.length)];//随机的字
					var deg = this.rn(-30,30);//字体的旋转角度
					context.setFontSize(18);
					context.setTextBaseline("top");
					let rc = await this.rc(80,150);
					context.setFillStyle(rc);
					context.save();
					context.translate(15*i+15,parseInt(h/1.5));
					context.rotate(deg*Math.PI/180);
					context.fillText(c,-15+5,-15);
					context.restore();
					str+=c;
				}
				uni.setStorage({
					key:'imgcode',
					data:str,
				});
				this.imgyzcode = str;
				for(var i=0;i<40;i++){
					context.beginPath();
					context.arc(this.rn(0,w),this.rn(0,h),1,0,2*Math.PI);
					context.closePath();
					context.setFillStyle(this.rc(150,200));
					context.fill();
				}
				context.draw();			
			},
			async rc(min,max){
				return new Promise((resolve, reject) => {
					var r= this.rn(min,max);
					var g= this.rn(min,max);
					var b= this.rn(min,max);
					resolve("rgb("+r+","+g+","+b+")");
				})
			},
			rn(max,min){
				return parseInt(Math.random()*(max-min))+min;
			},
			refresh:function(){
				this.init();
			},
			canvasIdErrorCallback: function (e) {
			    console.error(e.detail.errMsg)
			}
		},

	}
